import {
  Box,
  Container,
  // Grid,
  makeStyles
} from '@material-ui/core';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import DialogDefault from 'src/components/Dialog/default';
import Page from 'src/components/Page';
import Table from 'src/components/Table';
import { SETTING } from 'src/constants';
import {
  deleteListIdProduct as deleteListItemId, deleteProduct,
  fetchProduct as fetchItem,

  setProductDeleteStateDefault as setDeleteStateDefault
} from 'src/saga/action';
import * as SELECTOR from 'src/saga/redux-selector';
import MuiTableColumn from './MuiTableColumn';
import Toolbar from './Toolbar';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  },
  productCard: {
    height: '100%'
  },
  MuiTableCss: {
    width: '100%'
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const columns = MuiTableColumn;
const initDialog = {
  status: false,
  title: '',
  content: '',
  redirectTo: '',
  rejectFn: () => { },
  confirmFn: () => { }
}

const ProductList = () => {

  const dataProduct = useSelector(state => SELECTOR.productSelector(state)); // Ly danh sch items hin th
  const statusProductDelete = useSelector(state => SELECTOR.productDeleteStatus(state)); // trng thi khi xa mt hoc nhiu item trong danh sch
  const dataCount = useSelector(state => SELECTOR.productCountSelector(state)); // Tng s lng danh sch item

  const dispatch = useDispatch();
  const classes = useStyles();

  const [open, setOpen] = React.useState(initDialog);

  useEffect(() => {
    dispatch(fetchItem({ limit: SETTING.LIST_ITEM_PER_PAGE }))
  }, [])

  /**
   * X l s kin tr kt qu v khi *xa* thnh cng
   */
  useEffect(() => {
    if (typeof statusProductDelete !== 'undefined') {
      if (statusProductDelete) { // Khi xa thnh cng
        dispatch(setDeleteStateDefault())
        dispatch(fetchItem({ limit: SETTING.LIST_ITEM_PER_PAGE }))
      } else { // Cha c xa

      }
    }
  }, [statusProductDelete])

  const handleTableOnlick = ({ type, dataIndex, rowIndex }) => (event) => {
    switch (type) {
      case "delete":
        setOpen({
          ...open,
          status: true,
          title: "Bn c mun xa?",
          content: dataProduct[dataIndex].title,
          rejectFn: () => { setOpen(initDialog) },
          confirmFn: () => {
            dispatch(deleteProduct({ id: dataProduct[dataIndex].productId }))
            setOpen(initDialog)
          }
        })
        break;
      case "edit":
        setOpen({
          ...open,
          status: true,
          title: "Bn c mun thay i?",
          content: dataProduct[dataIndex].title,
          rejectFn: () => {
            // dispatch(getProduct({ id }))
            setOpen(initDialog)
          },
          redirectTo: "/products/edit/" + dataProduct[dataIndex].productId,
          confirmFn: () => {
            setOpen(initDialog)
          }
        })
        break;
      default:
        break;
    }
  }

  /**
   * X l s kin: Xa danh sch  chn trong table
   * @param {*} data [object]
   */
  const deleteAllList = (data) => {
    if (data && data.length > 0) {
      const list = data.map(data => data.productId)
      if (list && list.length > 0) {
        const listTitle = data.map(data => " " + data.name)
        setOpen({
          ...open,
          status: true,
          title: "Bn c mun xa danh sch Product  chn?",
          content: listTitle.toString().trim(),
          rejectFn: () => {
            setOpen(initDialog)
          },
          confirmFn: () => {
            dispatch(deleteListItemId({ list }))
            setOpen(initDialog)
          }
        })
      }
    }
  }

  return (
    <Page
      className={classes.root}
      title="Products"
    >
      <Container maxWidth={false}>
        <Toolbar pathRedirect={'/products/create'} />
        <Box
          mt={3}
          display="flex"
          justifyContent="center"
        >
          <Table
            dataTable={dataProduct}
            dataCountAll={dataCount}
            columns={columns({ handleTableOnlick })}
            fetchItem={fetchItem}
            deleteItem={deleteAllList}
          ></Table>
        </Box>
      </Container>
      <DialogDefault open={open}></DialogDefault>
    </Page>
  );
};

export default ProductList

