import {
    Button, Dialog,



    DialogActions, DialogContent,
    DialogContentText, DialogTitle, makeStyles
} from '@material-ui/core';
import React from 'react';

const useStyles = makeStyles((theme) => ({
    DialogCss: {
        minWidth: '320px'
    }
}));

export default ({
    open: {
        status, // ng m popup
        title, // tiu  popup
        redirectTo, // ty chn redirect khi kt thc (confirm)
        confirmFn = () => { }, // s kin khi confirm
        rejectFn = () => { } // s kin khi reject
    },
    disableFooter = false, // n Thanh Footer ca popup true -> n
    children
}) => {
    const classes = useStyles()
    return (
        <>
            <Dialog
                open={status}
                onClose={() => {
                    rejectFn()
                }}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle
                    className={classes.DialogCss}
                    id="alert-dialog-title">{title}</DialogTitle>
                <DialogContent>
                    {children}
                </DialogContent>
                {
                    disableFooter ? ("") : (
                        <DialogActions>
                            <Button
                                color="primary"
                                onClick={() => confirmFn()}
                                href={redirectTo}
                                component={redirectTo == '' ? 'button' : 'a'}
                            >ng </Button>
                            {
                                redirectTo == '' ? (
                                    <Button
                                        style={{ color: 'red' }}
                                        color="primary"
                                        onClick={() => {
                                            rejectFn()
                                        }}>Hy b</Button>
                                ) : ""
                            }
                        </DialogActions>
                    )
                }

            </Dialog>
        </>
    );
}