import {

    Button,
    Container,

    Grid,

    makeStyles,
    Paper,

    TextField, Typography
} from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import EditIcon from '@material-ui/icons/Edit';
import Tooltip from '@material-ui/core/Tooltip';
import AddIcon from '@material-ui/icons/Add';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import SortIcon from '@material-ui/icons/Sort';
import { useRowGutterStyles } from '@mui-treasury/styles/gutter/row';
import arrayMove from 'array-move';
import { ContentState, convertToRaw, EditorState } from 'draft-js';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import { DropzoneDialog } from 'material-ui-dropzone';
import React, { useEffect } from 'react';
import { Editor } from 'react-draft-wysiwyg';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { toast } from "react-toastify";
import DialogDefault from 'src/components/Dialog/default';
import Page from 'src/components/Page';
import { SETTING } from 'src/constants';
import EmojisBasic from 'src/libs/Emojis/basic';
import {
    actionUploadFile,
    fetchPageUpdate,

    getDetailItem,
    setDefaulGetPage
} from 'src/saga/action';
import * as SELECTOR from 'src/saga/redux-selector';
import { UploadFile } from '../../../libs/apiApp';

const KEY_FILTER = "image_main"
const typeReducer = "Page"

const SortableItem = SortableElement(({ index, value }) => {
    const classes = useStyles();
    return (
        <>
            <Paper style={{
                paddingBottom: 15,
                listStyleType: 'none'
            }} className={classes.paper}>
                <li>
                    <h4>{value.title}</h4>
                </li>
            </Paper>
        </>
    )
});

const SortableList = SortableContainer(({ items }) => {
    return (
        <ul>
            <Paper>
                {
                    items.map((value, index) => {
                        return (
                            <>
                                <SortableItem
                                    key={`item-${value.id}`}
                                    index={index}
                                    value={value}
                                />
                            </>
                        )
                    })
                }
            </Paper>
        </ul>
    );
});

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.dark,
        minHeight: '100%',
        paddingBottom: theme.spacing(3),
        paddingTop: theme.spacing(3)
    },
    title: {
        width: '100%'
    },
    paper: {
        padding: '10px 30px 30px 30px'
    },
    rowSpaceEvenly: {
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start'
    },
    rowBetween: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center'
    },
    EditorLayout: {
        minHeight: '60vh'
    }
}));

const initValues = {
    title: '',
    key: '',
    url: '',
    content: '',
    element: [],
    setting: {}
};

const initDialog = {
    status: false,
    title: '',
    content: '',
    redirectTo: '',
    rejectFn: () => { },
    confirmFn: () => { }
};

const CURDPage = () => {
    const dispatch = useDispatch();
    const classes = useStyles();
    const styles = useRowGutterStyles({ size: '1rem' });

    const detailItem = useSelector((state) => SELECTOR.pageDetial(state));

    const [values, setValues] = React.useState({ ...initValues });
    const [open, setOpen] = React.useState(initDialog);
    const [disSubmitButton, setDisSubmitButton] = React.useState(false);

    const [openEditContent, setOpenEditContent] = React.useState(false);
    const [openEditBigContent, setOpenEditBigContent] = React.useState(false);
    const [openSortContent, setOpenSortContent] = React.useState(false);

    const [inputContentTitle, setInputContentTitle] = React.useState("");
    const [inputContent, setInputContent] = React.useState("");
    const [inputIndexContent, setInputIndexContent] = React.useState(-1);

    const { id } = useParams();

    useEffect(() => {
        if (id) {
            dispatch(getDetailItem({ idItem: id, typeReducer }));
        }
        return () => {
            dispatch(setDefaulGetPage({ status: undefined }))
        }
    }, [id]);

    useEffect(() => {
        if (detailItem) {
            switch (detailItem.key) {
                case "about-us":
                    setValues({
                        title: detailItem.title ? detailItem.title : initValues.title,
                        key: detailItem.key ? detailItem.key : initValues.key,
                        url: detailItem.url ? detailItem.url : initValues.url,
                        content: detailItem.content ? detailItem.content : initValues.content,
                        element: detailItem.element ? detailItem.element : initValues.element,
                        setting: detailItem.setting ? detailItem.setting : initValues.setting,
                    });
                    break;
                default:
                    break;
            }
        } else {
            setValues({ ...initValues });
        }
    }, [detailItem]);

    /**
       * Change content with type input
       * @param {*} prop: key work
       * @param {*} event:
       */
    const handleChange = (prop) => (event) => {
        const dataSave = event.target.value;
        setValues({ ...values, [prop]: dataSave });
    };

    const handleCloseBigContent = (event) => {
        setOpenEditBigContent(false)
    }
    const handleOpenBigContent = (event) => {
        setOpenEditBigContent(true)
    }
    const handleCloseSortContent = (event) => {
        setOpenSortContent(false)
    }
    const handleOpenSortContent = (event) => {
        setOpenSortContent(true)
    }
    const handleOpenEditContent = (event) => {
        setOpenEditContent(true)
    }
    const handleCloseEditContent = (event) => {
        setInputIndexContent(-1)
        setOpenEditContent(false)
    }

    /**
     * handle when press submit && save to server
     * @param {*} event
     */
    const handleSubmit = (event) => {
        const dataSend = values;
        if (dataSend.title === '' || dataSend.title.length <= 0) {
            toast.warn('Tiu  khng c b trng');
            return;
        }
        if (dataSend.content === '' || dataSend.content.length <= 0) {
            toast.warn('Ni dung khng c b trng');
            return;
        }
        dispatch(fetchPageUpdate({ data: { ...dataSend }, _id: dataSend.key }));
        setDisSubmitButton(false);
    };

    /**
     * Change content wysiwyg
     * @param {*} data: key work
     * @param {*} editorState: 
     */
    const onEditorStateChange = data => (editorState) => {
        setInputContent(editorState);
    };

    const onSortEnd = ({ oldIndex, newIndex }) => {
        let swapData = arrayMove(values.element, oldIndex, newIndex)
        setValues({
            ...values,
            element: swapData
        })
    };

    return (
        <Page
            className={classes.root}
            title="Pages"
        >
            <Container maxWidth={false}>
                <Grid container spacing={3}>
                    <Grid item xs={12} md={9}>
                        <Paper className={classes.paper}>
                            <br />
                            <TextField
                                className={classes.title}
                                label="Tiu "
                                color="secondary"
                                value={values.title}
                                defaultValue={detailItem.title}
                                onChange={handleChange('title')}
                            />
                            <br />
                            <br />
                            <TextField
                                className={classes.title}
                                label="M t"
                                color="secondary"
                                value={values.content}
                                defaultValue={detailItem.content}
                                onChange={handleChange('content')}
                            />
                        </Paper>
                        <br />
                        {
                            values.element.map((data, index) => {
                                return (
                                    <Paper style={{ paddingBottom: '56px' }} className={classes.paper}>
                                        <h3>{data.title}</h3>
                                        <div className={`contentPage`} dangerouslySetInnerHTML={{ __html: data.description }} ></div>
                                        <div style={{ float: 'right' }}>
                                            <Tooltip title={`Xa ni dung "${data.title}"`} aria-label="add">
                                                <IconButton
                                                    onClick={() => {
                                                        setOpen({
                                                            ...open,
                                                            status: true,
                                                            title: data.title,
                                                            content: 'Bn c mun xa khng?',
                                                            confirmFn: () => {
                                                                let spliceData = values.element
                                                                spliceData.splice((index), 1)
                                                                setValues({
                                                                    ...values,
                                                                    element: spliceData
                                                                })
                                                                setOpen(initDialog);
                                                            },
                                                            rejectFn: () => {
                                                                setOpen(initDialog);
                                                            }
                                                        });
                                                    }}
                                                    aria-label="sort"
                                                    className={classes.margin}
                                                    style={{
                                                        color: "red"
                                                    }}
                                                >
                                                    <DeleteForeverIcon />
                                                </IconButton>
                                            </Tooltip>
                                            <Tooltip title={`Thay i ni dung "${data.title}"`} aria-label="add">
                                                <IconButton
                                                    onClick={() => {
                                                        setInputContentTitle(data.title)
                                                        const contentDetailsBlock = htmlToDraft(data.description);
                                                        if (contentDetailsBlock) {
                                                            const contentState = ContentState.createFromBlockArray(contentDetailsBlock.contentBlocks);
                                                            const editorState = EditorState.createWithContent(contentState);
                                                            setInputContent(editorState)
                                                        }
                                                        setInputIndexContent(index)
                                                        handleOpenEditContent()
                                                    }}
                                                    aria-label="sort"
                                                    className={classes.margin}
                                                    style={{
                                                        color: "#3f51b5"
                                                    }}
                                                >
                                                    <EditIcon />
                                                </IconButton>
                                            </Tooltip>
                                        </div>
                                    </Paper>
                                )
                            })
                        }
                        <br />
                        <Paper style={{ paddingBottom: '10px', display: 'flex' }} className={classes.paper}>
                            <div className={styles.parent}>
                                <Tooltip title="Sp xp ni dung" aria-label="add">
                                    <IconButton aria-label="sort" className={classes.margin} onClick={handleOpenSortContent}>
                                        <SortIcon />
                                    </IconButton>
                                </Tooltip>
                            </div>
                            <div className={styles.parent}>
                                <Tooltip title="Thm mi ni dung" aria-label="add">
                                    <IconButton aria-label="add" className={classes.margin} onClick={handleOpenBigContent}>
                                        <AddIcon />
                                    </IconButton>
                                </Tooltip>
                            </div>
                        </Paper>
                        <br />
                    </Grid>
                    {/* --------------------------------------------------------------------------------- */}
                    <Grid item xs>
                        <Paper className={classes.paper}>
                            <br />
                            <Typography
                                align="left"
                                color="textPrimary"
                                variant="caption"
                                display="block"
                                gutterBottom
                            >Ty chn</Typography>
                            <div className={classes.rowSpaceEvenly}>
                                <Button
                                    style={{ color: 'red', width: '60%' }}
                                    onClick={() => {
                                        setOpen({
                                            ...open,
                                            status: true,
                                            title: 'Quay tr li ban u',
                                            content: 'Hy b cc chnh sa ca bn  quay tr v ni dung ban u?',
                                            confirmFn: () => {
                                                setValues({
                                                    title: detailItem.title ? detailItem.title : initValues.title,
                                                    key: detailItem.key ? detailItem.key : initValues.key,
                                                    url: detailItem.url ? detailItem.url : initValues.url,
                                                    content: detailItem.content ? detailItem.content : initValues.content,
                                                    element: detailItem.element ? detailItem.element : initValues.element,
                                                    setting: detailItem.setting ? detailItem.setting : initValues.setting,
                                                });
                                                setOpen(initDialog);
                                            },
                                            rejectFn: () => {
                                                setOpen(initDialog);
                                            }
                                        });
                                    }}
                                >Quay tr li ban u</Button>
                                <Button color="secondary" style={{ width: '40%' }} onClick={handleSubmit} disabled={disSubmitButton}>Thay i</Button>
                            </div>
                        </Paper>
                    </Grid>
                    {/* --------------------------------------------------------------------------------- */}
                </Grid>
                <DialogDefault open={open} />
            </Container>

            <Dialog open={openEditBigContent} onClose={handleCloseBigContent} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Thm ni dung</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Mi ni dung bn thm vo s c cp nht ln trang <b>"About"</b>.<br />
                        <b>"Tiu  ni dung"</b> tng ng vi mc lc, bn c th sp xp li theo  mun.
                    </DialogContentText>
                    <br />
                    <TextField
                        autoFocus
                        className={classes.title}
                        label="Tiu  ni dung"
                        color="secondary"
                        value={inputContentTitle}
                        onChange={e => setInputContentTitle(e.target.value)}
                    />
                    <div className={classes.EditorLayout}>
                        <Editor
                            wrapperClassName="thangtm13_wrapper"
                            editorClassName="thangtm13_editor"
                            onEditorStateChange={onEditorStateChange("content")}
                            editorState={inputContent}
                            toolbar={{
                                image: {
                                    uploadCallback: UploadFile,
                                    alt: { present: true, mandatory: true },
                                    previewImage: true,
                                    inputAccept: SETTING.FILE_UPLOAD_IMAGE.toString(),
                                },
                                emoji: {
                                    emojis: EmojisBasic,
                                },
                            }}
                        />
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button
                        style={{ color: 'red' }}
                        onClick={() => {
                            setInputContent("")
                            setInputContentTitle("")
                        }}
                    >Nhp li</Button>
                    <Button
                        style={{ color: '#3f51b5' }}
                        onClick={() => {
                            if (inputContent) {
                                let data = values.element ? values.element : []
                                const description = draftToHtml(convertToRaw(inputContent.getCurrentContent()))
                                if (inputContentTitle.trim().length > 0) {
                                    data.push({
                                        id: values.element.length + 1,
                                        title: inputContentTitle.trim(),
                                        description: description
                                    })
                                }
                                setValues({
                                    ...values,
                                    element: data
                                })
                                setInputContent("")
                                setInputContentTitle("")
                            } else {
                                toast.warn('Ni dung khng tn ti');
                            }
                            handleCloseBigContent()
                        }}
                    >Thm ni dung</Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openEditContent} onClose={handleCloseEditContent} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Thay i ni dung</DialogTitle>
                <DialogContent>
                    <TextField
                        autoFocus
                        className={classes.title}
                        label="Tiu  ni dung"
                        color="secondary"
                        value={inputContentTitle}
                        onChange={e => setInputContentTitle(e.target.value)}
                    />
                    <div className={classes.EditorLayout}>
                        <Editor
                            wrapperClassName="thangtm13_wrapper"
                            editorClassName="thangtm13_editor"
                            onEditorStateChange={onEditorStateChange("content")}
                            editorState={inputContent}
                            toolbar={{
                                image: {
                                    uploadCallback: UploadFile,
                                    alt: { present: true, mandatory: true },
                                    previewImage: true,
                                    inputAccept: SETTING.FILE_UPLOAD_IMAGE.toString(),
                                },
                                emoji: {
                                    emojis: EmojisBasic,
                                },
                            }}
                        />
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button
                        style={{ color: 'red' }}
                        onClick={() => {
                            setInputContent("")
                            setInputContentTitle("")
                        }}
                    >Nhp li</Button>
                    <Button
                        style={{ color: '#3f51b5' }}
                        onClick={() => {
                            if (inputContent) {
                                let data = values.element ? values.element : []
                                const description = draftToHtml(convertToRaw(inputContent.getCurrentContent()))
                                if (inputContentTitle.trim().length > 0) {
                                    data[inputIndexContent] = {
                                        id: values.element.length + 1,
                                        title: inputContentTitle.trim(),
                                        description: description
                                    }
                                }
                                setValues({
                                    ...values,
                                    element: data
                                })
                                setInputContent("")
                                setInputIndexContent(-1)
                                setInputContentTitle("")
                            } else {
                                toast.warn('Ni dung khng tn ti');
                            }
                            handleCloseEditContent()
                        }}
                    >Thm ni dung</Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openSortContent} onClose={handleCloseSortContent} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Sp xp ni dung</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Ni dung bn sp xp s hin th tng ng ln trang <b>"About"</b>.<br />
                    </DialogContentText>
                    <br />
                    <Paper style={{ padding: '10px' }}>
                        <h4>Sp xp ni dung</h4>
                        <br />
                        <SortableList
                            items={values.element}
                            onSortEnd={onSortEnd}
                        />
                    </Paper>
                </DialogContent>
                <DialogActions>
                    <Button
                        style={{ color: 'red' }}
                        onClick={() => {
                            handleCloseSortContent()
                        }}
                    >ng </Button>
                </DialogActions>
            </Dialog>
        </Page>
    );
};

export default CURDPage;
